.map__wrapper {
  height: 55vw;
  max-height: 50rem;
  position: relative;
}

.map__container {
  background-color: $color--jelly-bean;
  bottom: unit(-2);
  left: unit(-1);
  margin: 0;
  padding: 0;
  position: absolute;
  right: unit(-1);
  top: 0;

  @include respond-to('medium-large') {
    bottom: unit(-2);
    left: 0;
    right: unit(-1);
    top: unit(-2);
  }
}
